css * {
	box-sizing: border-box;
}

html,
body {
	margin: 0;
	padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
	margin: 0;
	padding: 0;
	font-weight: normal;
}

ol,
ul {
	list-style: none;
}

a {
	text-decoration: none;
}

img {
	max-width: 100%;
	height: auto;
}

button,
button:active,
input:active,
input {
	outline: none;
	white-space: nowrap;
}

/*//////////// RESET ////////////*/

#root {
	height: 100%;
	/* width: 100vw; */
	/* overflow-x: hidden; */
	overflow-y: scroll;
	left: 0;
}
